import React, { Component } from 'react';
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';
import publicRoute from '../rotues/publicRoute';
import privateRoutes from '../rotues/privateRoute';
import Welcome from './Welcome';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Scrollbars } from 'react-custom-scrollbars';
import { Props, State, CRoute, PublicRoute } from '../interface/app';
import { I18nextProvider } from 'react-i18next';
import i18n from './../i18n';

class App extends Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      path: '/',
      scrollBar: undefined,
    };
  }

  componentDidMount = () => {
    const wHeight = window.innerHeight;
    this.setState({ path: window.location.pathname, scrollBar: wHeight });
  };

  publicRoute = (route: PublicRoute, index: number) => {
    return (
      <Route
        key={index}
        path={route.path}
        component={route.component}
        exact={route.exact}
      />
    );
  };

  authRoute = (route: CRoute, index: number) => {
    const { auth } = this.props;
    if (auth) {
      const isAllow = route.user.includes('owner');
      // const isAllow = route.user.includes(restaurant.role);
      if (isAllow) {
        return (
          <Route
            key={index}
            path={route.path}
            component={route.component}
            exact={route.exact}
          />
        );
      }
      return null;
    }
    return (
      <Redirect
        to={{
          pathname: '/login',
        }}
      />
    );
  };

  NoMatch = () => {
    const { auth } = this.props;
    if (auth) {
      return (
        <Redirect
          to={{
            pathname: '/dashboard',
          }}
        />
      );
    }
    return (
      <div>
        <h3>URL not found!!</h3>
      </div>
    );
  };

  render() {
    const { scrollBar } = this.state;
    return (
      <I18nextProvider i18n={i18n}>
        <Scrollbars autoHeightMax={scrollBar} autoHeight={true} {...this.props}>
          <BrowserRouter>
            <Switch>
              <Route key={'welcome'} exact path="/" component={Welcome} />
              {publicRoute.map((route, index) =>
                this.publicRoute(route, index)
              )}
              {privateRoutes.map((route, index) =>
                this.authRoute(route, index)
              )}
              <Route path="*">{this.NoMatch}</Route>
            </Switch>
          </BrowserRouter>
        </Scrollbars>
      </I18nextProvider>
    );
  }
}

const mapStateToProps = (state: Props) => {
  const { auth } = state;
  return { auth };
};

const mapDispatchToProps = (dispatch: any) => bindActionCreators({}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(App);
