import React from 'react';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import { Card, CardContent, Icon, Typography } from '@material-ui/core';

type Props = {
  title: string;
  details: string;
  icon: any;
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      maxWidth: '390px',
      marginTop: '30px',
      backgroundColor: '#ffffff',
      color: '#717173',
      padding: '45px 45px',
      borderRadius: '10px',
      boxShadow: '0 8px 20px -12px rgba(0,0,0,0.1)',
      transition: 'all .3s cubic-bezier(.645,.045,.355,1)',
      '&:hover': {
        backgroundImage: 'linear-gradient(90deg,#0baffb, #1a6cd4)',
        boxShadow: '0 10px 25px 10px rgba(0,0,0,.1)',
        color: '#ffffff',
        transform: 'translateY(-4px)',
      },
      [theme.breakpoints.down('sm')]: {
        backgroundImage: 'linear-gradient(90deg,#0baffb, #1a6cd4)',
        color: '#ffffff',
        padding: '32px 16px',
        borderRadius: '5px',
        marginTop: '16px',
      },
    },
    title: {
      fontSize: '18px',
      fontWeight: 700,
      margin: '16px 0',
    },
    details: {
      fontWeight: 300,
      fontSize: '18px',
      lineHeight: '30px',
    },
    content: {
      padding: '0  !important',
    },
  })
);

const ServiceCart = ({ title, details, icon }: Props) => {
  const classes = useStyles();

  return (
    <Card className={classes.root} elevation={0}>
      <CardContent className={classes.content}>
        <Icon
          component={icon}
          style={{
            width: '54px',
            height: '54px',
          }}
        />
        <Typography className={classes.title} component={'h5'}>
          {title}
        </Typography>
        <Typography variant="body2" className={classes.details}>
          {details}
        </Typography>
      </CardContent>
    </Card>
  );
};

export default ServiceCart;
